:root{--pink:#ff1a8c;--gray-soft:#555}
.altSection{}
.sectionInner{max-width:1100px;margin:0 auto;padding:40px 20px;display:flex;flex-direction:column;gap:18px;align-items:center}
.sectionTitle{font-family:var(--font-playfair),serif;color:var(--pink);font-size:1.6rem;margin:0 0 6px 0;text-align:center}
.checkList{display:grid;gap:10px;color:var(--gray-soft);max-width:980px}
.check{color:var(--pink);font-weight:900;margin-right:8px}
