:root {
  --pink: #ff1a8c;
  --pink-light: #ffd6ea;
  --gray-soft: #555;
}
.sectionWrapper {
}
.sectionInner {
  max-width: 1100px;
  margin: 0 auto;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  align-items: center;
}
.sectionTitle {
  font-family: var(--font-playfair), serif;
  color: var(--pink);
  font-size: 1.6rem;
  margin: 0 0 6px 0;
  text-align: center;
}
.sectionIntro {
  color: var(--gray-soft);
  margin: 0 0 18px 0;
  text-align: center;
  max-width: 820px;
}
.featureGrid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  width: 100%;
  justify-items: center;
}
.featureItem {
  width: 100%;
  max-width: 980px;
  display: grid;
  grid-template-columns: 40px 1fr;
  align-items: start;
  gap: 14px;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.98),
    rgba(255, 255, 255, 0.96)
  );
  border-radius: 14px;
  padding: 16px;
  box-shadow: 0 10px 28px rgba(0, 0, 0, 0.04);
  border: 2px solid rgba(255, 46, 122, 0.2);
}
.featureIcon {
  width: 40px;
  height: 40px;
  border-radius: 999px;
  background: linear-gradient(180deg, var(--pink), #ff6fbf);
  color: #fff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 900;
  font-size: 0.95rem;
}
.featureText {
  color: var(--pink);
  font-weight: 700;
  margin: 0;
}
@media (min-width: 768px) {
  .featureGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}
