:root{--pink:#ff1a8c;--gray-soft:#555}
.siteFooter{background:#fff;border-top:2px solid rgba(0,0,0,0.1);position:relative;padding-top:8px}
.footerInner{max-width:1100px;margin:0 auto;padding:18px 16px;display:flex;flex-direction:column;gap:8px;align-items:center}
.copy{color:var(--gray-soft);margin:0}
.footerNav{display:flex;gap:12px;flex-wrap:wrap}
.footerLink{color:var(--pink);text-decoration:none;font-weight:700}
.footerNote{max-width:1100px;margin:0 auto;padding:10px 16px;color:var(--gray-soft);text-align:center}
.siteFooter > svg{position:absolute;left:18px;top:-12px;opacity:0.95}
